        /* Base modal styling */
dialog.auth-modal {
    position:relative;
    box-sizing:border-box;
    border: none;
    border-radius: 8px;
    padding: 2rem;
    width: clamp(300px, 100%, 600px);
    box-shadow: 0 4px 10px 0px rgba(0,0,0,0.25);
    animation:appear 0.4s;
    font-size:1rem;
    transition: 0.4s;
   
    &:focus {
        outline:0;
    }
    &:focus-visible {
        outline:0px solid #000;
    }
    &:focus-within {
        background:#fff;
         outline:0;
    }

    /* Overlay effect */
    &::backdrop {
        position: relative;
        background: rgba(0,0,0,0.5);
        // backdrop-filter: blur(2px);
        background-blend-mode:color;


    }
    .modal-close {
        position: absolute;
        width: 40px;
        right: 40px;
        aspect-ratio: 1 / 1;
        font-size: 2rem;
    }

    h2 {
        
        font-size:1.5rem;
    }
    /* Form layout */
    .auth-form {
        display: flex;
        flex-direction: column;
        gap: 1rem;
        flex:1 1;

            &.hidden {
            display:none;
            }
    }

    .auth-form label {
        font-size:1rem;
        font-weight: 600;
    }

    .auth-form input {
        font-size:1rem;
        padding: 0.5rem;
        border: 1px solid #ccc;
        border-radius: 4px;
    }

    /* Action buttons */
    menu {
        display: flex;
        justify-content: flex-end;
        gap: 1rem;
    }

    menu button {
        padding: 0.5rem 1rem;
        border: none;
        border-radius: 4px;
        cursor: pointer;
    }

    menu button[type="submit"] {
        background: #ff7043; /* Recipe-Me accent color */
        color: white;
    }

    menu button[type="reset"] {
        background: #eee;
    }

    .login-register {
        display:flex;
        flex-direction: column;
    }
}/* end auth modal */



