// Splash overlay
.splash-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  z-index: 100;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 1;
  transition: opacity 0.5s ease, visibility 0.5s ease;
  
  &.hidden {
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
  }


}


  // Your floating food animations here

  #wheel-a, div#wheel-b, div#wheel-c {
    z-index: 2;
}
#wheel-a.spinning-wheel {
    position: absolute;
    top:-2rem;
    height:80%;
    width:80%;
    border-radius:50%;
    animation: spinningWheel 90s linear infinite reverse;
  
}
#wheel-b.spinning-wheel{
    position: absolute;
    top:-2rem;
    height:70%;
    width:70%;
    transform:scale(0.5);
    animation: spinningWheel 30s linear infinite reverse;
}
#wheel-c.spinning-wheel{
    position: absolute;
    top:-2rem;
    height:120%;
    width:70%;
    transform:scale(0.5);
    animation: spinningWheel 40s linear infinite reverse;
}

#wheel-b.spinning-wheel .fas{
    color:rgb(79, 11, 59);
    font-size:2rem;
    position:absolute;
    animation: spinningWheel 15s linear infinite;
}
#wheel-a.spinning-wheel .fas{
    color:rgb(96, 28, 110);
    font-size:2rem;
    position:absolute;
    animation: spinningWheel 3s linear infinite;
}
#wheel-c.spinning-wheel .fas{
    color:rgb(226, 85, 85);
    font-size:2rem;
    position:absolute;
    animation: spinningWheel 6s linear infinite;
}
    .spinning-wheel .fas:nth-of-type(1){
        top:-12rem;
        left:-12rem;

    }
    .spinning-wheel .fas:nth-of-type(2){
        top:-12rem;
        right:-12rem;

    }
    .spinning-wheel .fas:nth-of-type(3){
