#select-images[open] {
    display: block;
    width: clamp(300px, 85vw, 1100px); // ✅ Wider dialog
    max-height: 90vh;
    padding: 2rem;
    border: none;
    border-radius: 12px;
    background: white;
    overflow: visible;

    .image-viewport {
        width: 100%;
        overflow: hidden;
        position: relative;
        height: 500px; // ✅ Taller viewport (was 400px)
        border-radius: 8px;
        background: #f5f5f5;
        margin-bottom: 1rem;

        ul {
            padding: 0;
            margin: 0;
            list-style: none;
            display: flex;
            gap: 0;
            transition: transform 0.4s ease;
            height: 100%;

            li {
                flex: 0 0 100%;
                width: 100%;
                display: flex;
                align-items: center;
                justify-content: center;
                height: 100%;
            }
        }
    }
}

#select-images[open] figure {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 0;
    padding: 16px; // ✅ Less padding
}

#select-images[open] img {
    max-width: 100%; // ✅ Fill width
    max-height: 85%; // ✅ More height (was 70%)
    width: auto;
    height: auto;
    object-fit: contain;
}

#select-images figcaption {
    padding: 0.5rem 1rem;
    font-size: 0.9rem;
    color: #666;
    text-align: center;
    margin-top: 0.5rem;
}

.image-buttons {
    position: absolute;
    top: 50%;
    left: 0;
    right: 0;
    transform: translateY(-50%);
    display: flex;
    justify-content: space-between;
    padding: 0 1rem;
    z-index: 10;
    pointer-events: none;
}

.image-buttons button {
    pointer-events: all;
    width: 3rem;
    height: 3rem;
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    background: rgba(255, 255, 255, 0.95);
    border: 2px solid rgba(0, 0, 0, 0.1);
    cursor: pointer;
    transition: all 0.2s;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
}

.image-buttons button i {
    font-size: 1.5rem;
    color: #333;
}

.next,
.prev {
    background: rgba(255, 255, 255, 0.95);
    color: #000;
}

.next:hover:not(:disabled),
.prev:hover:not(:disabled) {
    background: #000;
    color: #fff;
    transform: scale(1.1);
}

.next:disabled,
.prev:disabled {
    opacity: 0.3;
    cursor: not-allowed;
    background: rgba(255, 255, 255, 0.5);
}

.count {
    text-align: center;
    margin: 1rem 0;
    font-size: 0.95rem;
    color: #666;
    font-weight: 500;
}

#prev-page,
#next-page,
