    border: 2px solid #ddd;
    background: white;
    font-size: 0.9rem;
    cursor: pointer;
    transition: all 0.2s;
}

#prev-page:hover:not(:disabled),
#next-page:hover:not(:disabled) {
    background: #f5f5f5;
    border-color: #999;
}

#select-image {
    background: #10b981;
