    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
    align-items: center;
    gap: 1rem;

}

.faas i {
    box-shadow: 0.3rem 0.4rem #bcbcbc;
    background-color: orangered;
    color: #fff;
    display: block;
    width: 100%;
    height: calc(16.6666666667vw - 2rem);
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    font-size: 4rem;
    border-radius: 0.875rem;
}

.faas i span {
    font-size: 0.875rem;
    color: #000;
    position: absolute;
    bottom: 0.4rem;
}

html {
    scroll-behavior: smooth;
    scroll-snap-type: y mandatory;

}

