}


/* Container */

.container {
    width: clamp(400px, 100%, 1024px);
    position: relative;
    margin: 0 auto;
    padding: 0 0rem;
    display: flex;
    flex-direction: column;

    z-index: 1;
}

/* Header */

.header {
    background: transparent;
    position: relative;
    color: $header-color;
    font-family: $headingText;
    padding: 1.6rem;
    margin-bottom: 3rem;
    //   text-align: center;
    z-index: 1;
}

.home-page header {
    margin-bottom: 2rem;
}

#categories-cloud {


    h3 {
        text-align: center;
        line-height:0;
        font-size:0.875rem;
    }

    display: flex;
    flex-direction: column;
    gap:1rem;
    background: #000;
    color:#fff;
    width: 100%;
    min-height:0px;
    height: 0;
    bottom: 0;
    padding: 0rem;
    margin:0;
    transform: scaleX(1) scaleY(0.9);
    animation:init 0.5s forwards;
    transform-origin: top;
}

#categories-cloud section {
    --n: calc(sibling-count() - sibling-index() + 1);
   display: flex;
    flex-wrap: wrap;
    align-self: center;
    justify-content: center;
    align-items: center;
    gap: 0.75rem;
   
}

#categories-cloud section label {
      animation-delay: calc(sibling-index() * 0.025s);
}
#categories-cloud :has(input) {
    opacity:0;
    transform: translate(-30px, 0);
    animation: init2 0.14s forwards .1s;
    transform-origin: top left;
}

.section-1  :is(label.checkbox) {
    font-size:1rem;
}
.checklist ul:has(label.checkbox) {
    gap:1rem;
    display:flex;
    flex-wrap: wrap;
}
.checklist ul:has(label.checkbox) li{
    --pink:lch(50.25% 90.11 347.93);
    display:flex;
    width:auto;
    display:inline-block;
    flex-direction: row;
    justify-content: center;
    align-items:center;
    gap:16px;
}
.checklist ul:has(label.checkbox) li .form-group {
    padding:0.5rem 0.8rem;
    border:1px solid var(--pink);
    border-radius:6px;
    height:60px;
    line-height:60px;
    display:flex;
    align-items:center;
    justify-content: center;
    flex-direction: row;

    label {
        cursor: pointer;
        height:inherit;
        

    }
}
.checklist ul:has(label.checkbox) li .form-group:has(input:checked) {
    background-color:var(--pink);
}
.checklist ul:has(label.checkbox) li .form-group input:checked {
    accent-color:pink;
}

.checklist ul:has(label.checkbox) li .form-group:has(input:checked) label {
    color:#fff;
}
@keyframes init {
    from {
        padding:0;
        transform: scaleX(1) scaleY(0.9);
        height:0;
    }
    to {
        padding:1rem;
        transform: scaleX(1) scaleY(1);
        height:auto;
    }
}
@keyframes init2 {
    from {
        opacity:.5;
        scale:0.1 .5;
        transform: translate(-30px, 0)
    }
    to {
        opacity:1;
        scale:1 1;
        transform: translate(0, 0);
    }
}

