/* Cloudinary Upload Section */
.image-upload-section {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  margin-bottom: 1rem;
}

.upload-option h3,
.search-option h3 {
  font-size: 1rem;
  margin-bottom: 0.5rem;
  color: #333;
}

.upload-button {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1.5rem;
  background: #ff6b6b;
  color: white;
  border-radius: 6px;
  cursor: pointer;
  font-weight: 500;
  transition: background 0.2s;
}

.upload-button:hover {
  background: #ff5252;
}

.upload-divider {
  text-align: center;
  position: relative;
  margin: 1rem 0;
}

.upload-divider::before,
.upload-divider::after {
  content: '';
  position: absolute;
  top: 50%;
  width: 45%;
  height: 1px;
  background: #ddd;
}

.upload-divider::before {
  left: 0;
}

.upload-divider::after {
  right: 0;
}

.upload-divider span {
  padding: 0 1rem;
  background: white;
  color: #999;
  font-size: 0.9rem;
}

#upload-status {
  margin-top: 0.5rem;
  min-height: 1.5rem;
}

.upload-status-uploading {
  color: #3498db;
}

.upload-status-success {
  color: #2ecc71;
}

.upload-status-error {
  color: #e74c3c;
}

.remove-image-btn {
  margin-top: 0.5rem;
  padding: 0.5rem 1rem;
  background: #e74c3c;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
}

.remove-image-btn:hover {
  background: #c0392b;
}

