/* Image Gallery */
.image-gallery {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 1rem;
  margin-top: 1rem;
}

.image-card {
  border: 2px solid #ddd;
  border-radius: 8px;
  padding: 0.5rem;
  background: white;
  cursor: move;
  transition: all 0.2s;
}

.image-card.dragging {
  opacity: 0.5;
  transform: scale(1.05);
}

.image-card:hover {
  border-color: #ff6b6b;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
}

.image-preview {
  position: relative;
  width: 100%;
  aspect-ratio: 16/9;
  overflow: hidden;
  border-radius: 4px;
  margin-bottom: 0.5rem;
}

.image-preview img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.image-preview video {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
